.main-menu {
  z-index: 900;
  display: block; }
  .main-menu .overlay {
    z-index: 901;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: black;
    opacity: 0;
    transform: translateX(100%);
    transition: opacity 0.2s linear 0s, transform 0s ease 0.2s; }
  .main-menu .menu-container {
    display: flex;
    flex-direction: column;
    z-index: 902;
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    min-width: 11em;
    max-width: 80%;
    background-color: white;
    color: black;
    transform: translateX(100%);
    transition: transform 0.16s ease-in 0s; }
  .main-menu.open .overlay {
    opacity: 0.4;
    transform: translateX(0);
    transition: opacity 0.2s linear 0s, transform 0s ease 0s; }
  .main-menu.open .menu-container {
    overflow-y: auto;
    transform: translateX(0);
    transition: transform 0.2s ease-out 0s; }
  .main-menu .close-menu {
    position: absolute;
    top: 0;
    right: 0; }
  .main-menu .links {
    margin-top: 70px;
    list-style: none;
    padding-left: 0;
    text-align: right;
    font-size: 1.2em;
    font-weight: bold;
    font-family: "Lucida Sans Unicode", "Lucida Grande", sans-serif; }
    .main-menu .links .link a {
      display: block;
      padding: 0.6em 1em;
      color: #404040;
      text-decoration: none; }
      .main-menu .links .link a:hover {
        background-color: #EEE; }
      .main-menu .links .link a:active {
        color: white;
        background-color: #404040; }
