.hashtag-intro {
  background-color: black;
  box-sizing: border-box;
  height: calc(100vh - 3em);
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: Arial;
  margin-left: auto;
  margin-right: auto;
  max-width: 600px; }
  .hashtag-intro .title {
    word-break: break-all;
    font-size: 1.5em;
    margin-top: 0;
    margin-bottom: 0.5em; }
  .hashtag-intro .desc {
    text-transform: uppercase;
    font-weight: 500;
    margin-bottom: 5em;
    line-height: 1.5em;
    margin-top: 0; }
