.signup-form .input-row {
  margin-bottom: 1em; }

.signup-form .input {
  padding: 1rem;
  background: transparent;
  color: #FFF;
  border: 1px solid #FFF;
  display: block;
  width: 100%; }
  .signup-form .input::placeholder {
    color: #FFF; }

.signup-form .help-text {
  display: block; }

.signup-form .help-text--has-error {
  color: red; }

.signup-form .desc {
  color: #666;
  font-size: 0.8em; }

.signup-form .submit {
  margin-top: 0.6em;
  font-weight: 600; }

.signup-form .link {
  color: inherit;
  text-decoration: underline;
  text-decoration-color: white; }
  .signup-form .link:hover {
    color: white; }
