.splash-screen {
  background-color: black;
  position: absolute;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  animation: fadeout 2s 0s;
  opacity: 0; }
  .splash-screen .logo {
    width: 50vw;
    max-width: 500px;
    margin: auto; }

@keyframes fadeout {
  0% {
    opacity: 1; }
  40% {
    opacity: 1; }
  100% {
    opacity: 0; } }
